@import "../../../../../common";

.List {
  header {
    user-select: none;
    padding: $padding;
    border: $border;
    border-radius: $border-radius;
    box-shadow: $shadow;
    background-color: $light-background-color;
    text-decoration: none;
  }

  ul {
    display: grid;
    grid-auto-rows: max-content;
    grid-template-columns: 100%;
    grid-gap: $margin;
    overflow-y: auto;
    height: 100%;
    scrollbar-width: none;
    box-sizing: border-box;
  }

  .dropdown {
    box-shadow: $shadow;
    background-color: $light-background-color;
    position: absolute;
    list-style: none;
    border: $border;
    border-radius: $border-radius;
    border-top-left-radius: 0;
    user-select: none;
    box-sizing: border-box;
    height: inherit;
    z-index: $dropdown-z-index;
  }

  .upper, .lower {
    position: sticky;
    border: none;
    background-color: transparent;
    box-shadow: none;
    padding: 0;
    float: right;
    opacity: .5;

    svg {
      max-width: $icon-size + .2em;
      max-height: $icon-size + .2em;
    }
  }

  .lower {
    bottom: 0;
  }

  .upper {
    top: 0;
  }
}

.List.expanded {
  header {
    border-color: $second-color;
    border-bottom-color: transparent;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }

  .dropdown {
    border-top-left-radius: 0;
    border-color: $second-color;
  }
}
